<script>
export default {


  data () {
    return {

      value: '',

    


      pagination: { // 
        currentPage: 1, // 
        pageSize: 10, // 
        total: 0, // 
        queryString: null // 
      },

    
    
    
    dataList: [], // 



     
    
    }
  },


  created () {


    this.findPage()
 
   

  },


  methods: {



   

    



//   
     findPage () {
    
     this.$axios.get('/comment/getAllCommentList/'+this.pagination.currentPage+'/'+this.pagination.pageSize).then(res=>{
       
                         
                         this.pagination.pageSize = res.data.data.size;
                         this.pagination.currentPage = res.data.data.current;
                         this.pagination.total = res.data.data.total;
                       
                       
                          this.dataList = res.data.data.records;    
                           console.log('admin_comments',res.data.data.records)
                       
                       

                          
     })    


     },









    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
      // vuex
      window.location.reload()
    },


    // 
    handleCurrentChange (currentPage) {
      // 
      this.pagination.currentPage = currentPage
      // findPage
      this.findPage()
    },



    handleSizeChange (newSize) {
      this.pagination.pageSize = newSize
      this.findPage()
    },



    // 
    resetForm () {
      this.formData = {}// 
      this.$refs.dataAddForm.resetFields()
    },


    // 
    handleCreate () {
      this.dialogFormVisible = true
      this.resetForm()
    },


//             

    handleDelete (commentid) {
     
     this.$confirm("","",{type:"info"}).then(()=>{
           
                    this.$axios.delete('/comment/'+commentid).then((res)=>{


                            if(res.data.flag)
                            this.$message.info("");

                      this.findPage()
                      this.$router.push('/AdminHome/Comments')   


                    })
                }).catch(()=>{
                    this.$message.info("");

    

      
         

     })
    },

    
    handleUpdate (blogid) {
      
      sessionStorage.setItem('admin_blogid',blogid)
      this.$router.push('/AdminHome/Postblogs')
     
    }

  }



}
</script>

