<template>
  <div class="blogs">
    <div class="content-header">
      <h1><small></small></h1>
      <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
        <el-breadcrumb-item ></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!---->
    <div class="app-container">
    <div class="box">
<!--    <div  class="m-container-small m-padded-tb-big">-->
      <div class="filter-container">
       
        
       

         <el-row>
            <el-col :span="6">
               <el-select v-model="value" placeholder=""  style="margin-left:50px">
            <el-option
              v-for="item in typeList"  
              :key="item.typeid"
              :label="item.typename"
              :value="item.typename"
              @click.native="findBlogBytype(item.typeid)">
            </el-option>

        </el-select> 
            </el-col>


            <el-col :span="4">
             <el-button size="mini" type="warning" @click.native="findPage"></el-button>
            </el-col>
            
          </el-row>

      
     
      </div>


      <div class="ui container">


        <el-table size="middle" current-row-key="id" :data="dataList" stripe highlight-current-row>
          <!--                        id,-->
          <el-table-column prop="index" type="index" min-width="15px" align="center" label=""></el-table-column>
        
          <el-table-column prop="title" label="" align="center"></el-table-column>
        
          <el-table-column prop="type.typename" label="" align="center"></el-table-column>
        

        <!--  slot   -->
          <el-table-column label="" prop="recommend">


            <template slot-scope="scope">
              
              <el-switch v-model="scope.row.recommend"
                         @change="recommendStateChanged(scope.row)">
              </el-switch>

            </template>

          </el-table-column>





         
          <el-table-column prop="createTime" label="" align="center"></el-table-column>
        
        
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click.native="handleDelete(scope.row.blogid)"></el-button>
              <el-button size="mini" type="primary" @click.native="handleUpdate(scope.row.blogid)"></el-button>
              <!--              <el-button size="mini" type="info" @click="handleForbidden(scope.row)"></el-button>-->
            </template>
          </el-table-column>


        </el-table>


        <div class="pagination-container">
          <el-pagination
            class="pagiantion"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pagination.currentPage"
            :page-sizes="[2,3,4,5]"
            :page-size="pagination.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total">
          </el-pagination>
        </div>
        <!--   *   -->
        <div class="add-form">
          <el-dialog title="" :visible.sync="dialogFormVisible">
            <el-form ref="dataAddForm" :model="formData" :rules="rules" label-position="right"
                     label-width="100px">
              <el-row>
                <el-col :span="12">
                  <el-form-item label="" prop="typeId">
                    <el-input v-model="formData.typeId"/>
                  </el-form-item>
                </el-col>
                <el-col :span="12">
                  <el-form-item label="" prop="name">
                    <el-input v-model="formData.name"/>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false"></el-button>
              <el-button type="primary" @click="handleAdd()"></el-button>
            </div>
          </el-dialog>
        </div>
      </div>
    </div>
    </div>
    <br>
    <br>
  </div>
</template>

<script>
export default {


  data () {
    return {

      value: '',

      options: [
        { label: '' }
        
      ],


      pagination: { // 
        currentPage: 1, // 
        pageSize: 10, // 
        total: 0, // 
        queryString: null // 
      },
      formData: {}, // 
      dialogFormVisible: false, // 
      dialogFormVisible4Edit: false, // 
    
    
    dataList: [], // 

    typeList:[],

     
     
      avatar: '',
      rules: { // 
        // 
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' }
        ]
      }
    }
  },


  created () {


    this.findPage()
 
    this.getTypeList()

  },


  methods: {



   

      getTypeList () {
     

          this.$axios.get('/home/getType').then(res=>{
              console.log('type',res.data)
             this.typeList = res.data.data 

             

     })     



    },




// 
     findPage () {
    
     this.$axios.get('/blog/findAdminHomePage/'+this.pagination.currentPage+'/'+this.pagination.pageSize).then(res=>{
       
                          console.log('home',res.data)
                         this.pagination.pageSize = res.data.data.size;
                         this.pagination.currentPage = res.data.data.current;
                         this.pagination.total = res.data.data.total;
                       
                       
                          this.dataList = res.data.data.records;    
                       
                       

                          
     })    


     },







     findBlogBytype(typeid)
     {            
                  
       this.$axios.get('/home/findAdmin_Blogs_ByType/'+this.pagination.currentPage+'/'+this.pagination.pageSize+'/'+typeid).then(res=>{
       
                          console.log('admin_typeByid',res.data)
                         this.pagination.pageSize = res.data.data.size;
                         this.pagination.currentPage = res.data.data.current;
                         this.pagination.total = res.data.data.total;
                       
                       
                       
                          this.dataList = res.data.data.records;           
              

     })    



     },








    // 
    handleAdd () {
      // 
      this.$refs.dataAddForm.validate((valid) => {


        if (valid) {
          // ajax
          // const param = this.$encrypTion(JSON.stringify(this.formData))
          this.$http.post('/api/types2/add', this.formData).then((res) => {
            // 
            this.dialogFormVisible = false
            if (res.data.flag) {
              // 
              this.findPage()
              // 
              this.$message({
                message: '',
                type: 'success'
              })
            } else { // 
              this.$message.error(res.data.code)
            }
          })
        } else { // 
          this.$message.error('')
          return false
        }
      })
    },


    //  switch 
     recommendStateChanged (row) {
      

      console.log('row',row)

       if(row.recommend==true) {

            // put
        this.$axios.put('/blog',row).then(res=>{ 

            if(res.data.flag){
              this.$message.success("")
            }

        })

       }

       else{ 

         // put
        this.$axios.put('/blog',row).then(res=>{ 

            if(res.data.flag){
              this.$message.error("")
            }

        })
       
      }
       
      
     },


    


    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
      // vuex
      window.location.reload()
    },


    // 
    handleCurrentChange (currentPage) {
      // 
      this.pagination.currentPage = currentPage
      // findPage
      this.findPage()
    },



    handleSizeChange (newSize) {
      this.pagination.pageSize = newSize
      this.findPage()
    },



    // 
    resetForm () {
      this.formData = {}// 
      this.$refs.dataAddForm.resetFields()
    },


    // 
    handleCreate () {
      this.dialogFormVisible = true
      this.resetForm()
    },


    handleDelete (blogid) {
     


     this.$confirm("","",{type:"info"}).then(()=>{
           
                    this.$axios.delete('/blog/'+blogid).then((res)=>{


                            if(res.data.flag)
                            this.$message.info("");

                      this.findPage()
                      this.$router.push('/AdminHome/Blogs')   


                    })
                }).catch(()=>{
                    this.$message.info("");

                
     })
    },

  
    handleUpdate (blogid) {
      
      sessionStorage.setItem('admin_blogid',blogid)
      this.$router.push('/AdminHome/changeBlogs')
     
    }

  }



}
</script>

<style lang="less" scoped>
  @import "../../assets/css/style.css";
  @import "../../assets/css/me.css";
  .blogs {
    height: 100%;
    margin: 0;
    padding: 0;
  }
</style>
