<template>
    <div id="myChart" :style="{width: '800px', height: '600px',marginLeft:'350px'}"></div>
</template>
<script>
    export default {
        name: 'Pie',
        mounted(){
         
             this.$axios.get('/echart/sumPie').then(res=>{ 
                 //    
          const echarts = require('echarts')
          // domecharts
        let myChart = echarts.init(document.getElementById('myChart'))
                // 
                myChart.setOption({
                   
                   tooltip: {
    trigger: 'item'
  },
  legend: {
    top: '5%',
    left: 'center'
  },
  series: [
    {
      name: '',
      type: 'pie',
      radius: ['40%', '70%'],
      avoidLabelOverlap: false,
      label: {
        show: false,
        position: 'center'
      },
      emphasis: {
        label: {
          show: true,
          fontSize: 40,
          fontWeight: 'bold'
        }
      },
      labelLine: {
        show: false
      },
      data: res.data.data
    }
  ]
                });
            })
        }
    }
</script>