<template>
  <div class="blogs">
    <div class="content-header">
      <h1><small></small></h1>
      <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
        <el-breadcrumb-item ></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!---->
    <div class="app-container">
      <div class="box">

        <div class="filter-container">
          





        

          <el-button type="primary" class="butT" @click="dialogFormVisible1=true" style="margin-left:50px"></el-button>


        </div>

      <div class="ui container">
        <el-table size="middle" current-row-key="id" :data="typeList" stripe highlight-current-row>
          <!--                        id,-->
          <el-table-column prop="typeid" min-width="15px" align="center" label=""></el-table-column>
          <el-table-column prop="typename" label="" align="center"></el-table-column>
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click="handleDelete(scope.row.typeid)"></el-button>
            <el-button size="mini" type="success" @click="handleUpdate(scope.row.typeid)"></el-button>

              <!--              <el-button size="mini" type="info" @click="handleForbidden(scope.row)"></el-button>-->
            </template>
          </el-table-column>
        </el-table>
        <div class="pagination-container">
          <el-pagination
            class="pagiantion"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pagination.currentPage"
            :page-sizes="[2,5,10,15]"
            :page-size="pagination.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total">
          </el-pagination>
        </div>
       
       
        <!--   *      el-dialog  -->
      
       <div class="add-form">



         <el-dialog title="" :visible.sync="dialogFormVisible1">
         
         
           <el-form ref="dataAddForm" :model="formData" :rules="rules" label-position="right"  label-width="100px">
             <el-row>
               
               <el-col :span="12">
                 <el-form-item label="" prop="typename">
                   <el-input v-model="formData.typename"/>
                 </el-form-item>
               </el-col>
             </el-row>


             
           </el-form>



           <div slot="footer" class="dialog-footer">
             <el-button @click="cancel1"></el-button>
             <el-button type="primary" @click="handleAdd()"></el-button>
           </div>
         </el-dialog>


       </div>








<!--                      -->
         <el-dialog title="" :visible.sync="dialogFormVisible2">
         
         
           <el-form ref="dataAddForm" :model="formData" :rules="rules" label-position="right"  label-width="100px">
             <el-row>
               
               <el-col :span="12">
                 <el-form-item label="" prop="typename">
                   <el-input v-model="formData.typename"/>
                 </el-form-item>
               </el-col>
             </el-row>


             
           </el-form>



           <div slot="footer" class="dialog-footer">
             <el-button @click="cancel2"></el-button>
             <el-button type="primary" @click="Update"></el-button>
           </div>
         </el-dialog>





          <AddTypes ref="addTypes"></AddTypes>




      </div>
    </div>
    </div>
    <br>
    <br>
  </div>
</template>


<script>

import AddTypes from '../../components/admin/AddTypes'


export default {
  components: {
    AddTypes
  },
  data () {

    return {


      pagination: { // 
        currentPage: 1, // 
        pageSize: 10, // 
        total: 0, // 
        queryString: null // 
      },

                  //    
      formData: {
        typeid:'',
        typename:''
      }, 


      dialogFormVisible1: false, // 
      dialogFormVisible2: false, // 
     
     typeList: [], //    


      user: {},
      nickname: '',
      // 
      avatar: '',


      rules: { // 
       
       
       // 
        typename: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' }
        ]
      }
    }
  },


  created () {
    this.getTypeList()
  },


  methods: {


    cancel1(){
      this.dialogFormVisible1=false
      this.formData.typename= ''
    },

    cancel2(){
      this.dialogFormVisible2=false
      this.formData.typename= ''
    },
  getTypeList () {
     
          this.$axios.get('/home/getType').then(res=>{
              console.log('type',res.data)
             this.typeList = res.data.data 
           
     })     

    },

  

      // 
  handleAdd () {
    
          
          this.$axios.post('/type', this.formData).then((res) => {
            // 
            this.dialogFormVisible1 = false
             this.formData.typename = ''
            if (res.data.flag) {
              // 
              this.getTypeList()
              // 
              this.$message({
                message: '',
                type: 'success'
              })
            } else { // 
              this.$message.error(res.data.code)
            }
          })
 
    },



      //    


      handleUpdate(typeid){

            this.dialogFormVisible2=true
            sessionStorage.setItem('typeid',typeid)

      },


      Update(){
        this.formData.typeid = sessionStorage.getItem('typeid')

        
        console.log(this.formData.typeid)
this.$axios.put('/type', this.formData).then((res) => {
            // 
            this.dialogFormVisible2 = false
             this.formData.typename = ''
            if (res.data.flag) {
              // 
              this.getTypeList()
              // 
              this.$message({
                message: '',
                type: 'success'
              })
            } else { // 
              this.$message.error(res.data.code)
            }
          })

      },



    ///      



     handleDelete (typeid){
     


     this.$confirm("","",{type:"info"}).then(()=>{
           
                    this.$axios.delete('/type/'+typeid).then((res)=>{


                            if(res.data.flag)
                            this.$message.success("")

                      this.getTypeList()
                       


                    })
                }).catch(()=>{
                    this.$message.info("");

    

      
         

     })
    },


    
    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
      // vuex
      window.location.reload()
    },



    // 
    handleCurrentChange (currentPage) {
      // 
      this.pagination.currentPage = currentPage
      // getTypeList
      this.getTypeList()
    },


    handleSizeChange (newSize) {
      this.pagination.pageSize = newSize
      this.getTypeList()
    },



    // 
    handleCreate () {
      // this.dialogFormVisible = true
      this.$refs.addTypes.handleCreate()
    }


   
  }
}
</script>

<style lang="less" scoped>
  @import "../../assets/css/me.css";
  @import "../../assets/css/style.css";
</style>
