<template>
  <div class="blogs">
    <div class="content-header">
      <h1><small></small></h1>
      <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
        <el-breadcrumb-item ></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <!---->
    <div class="app-container">
    <div class="box">
<!--    <div  class="m-container-small m-padded-tb-big">-->
      <div class="filter-container">
       

      </div>


      <div class="ui container">


        <el-table size="middle" current-row-key="id" :data="dataList" stripe highlight-current-row>
          <!--                        id,-->
          <el-table-column prop="uid" type="index" min-width="15px" align="center" label=""></el-table-column>
        
          <el-table-column prop="username" label="" align="center"></el-table-column>
        
          
        
        


        
        
          <el-table-column label="" align="center">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click.native="handleDelete(scope.row.uid)" ></el-button>
              <el-button size="mini" type="primary" @click.native="handleUpdate(scope.row.uid)"></el-button>
              <!--              <el-button size="mini" type="info" @click="handleForbidden(scope.row)"></el-button>-->
            </template>
          </el-table-column>


        </el-table>


        <div class="pagination-container">
          <el-pagination
            class="pagiantion"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pagination.currentPage"
            :page-sizes="[2,3,4,5]"
            :page-size="pagination.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total">
          </el-pagination>
        </div>
       
      </div>
    </div>
    </div>
    <br>
    <br>
  </div>
</template>

<script>
export default {


  data () {
    return {
       
      value: '',

      options: [
        { label: '' }
        
      ],


      pagination: { // 
        currentPage: 1, // 
        pageSize: 10, // 
        total: 0, // 
        queryString: null // 
      },
      formData: {}, // 
      dialogFormVisible: false, // 
      dialogFormVisible4Edit: false, // 
    
    
    dataList: [], // 

    typeList:[],

     
     
      avatar: '',
      rules: { // 
        // 
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 15, message: ' 2  15 ', trigger: 'blur' }
        ]
      }
    }
  },


  created () {


    this.findPage()
 
  

  },


  methods: {


// 
     findPage () {
    
     this.$axios.get('/user/'+this.pagination.currentPage+'/'+this.pagination.pageSize).then(res=>{
       
                          console.log('admin_users',res.data)
                         this.pagination.pageSize = res.data.data.size;
                         this.pagination.currentPage = res.data.data.current;
                         this.pagination.total = res.data.data.total;
                       
                       
                          this.dataList = res.data.data.records;    
                       
                        
     })    


     },




    // 
    handleAdd () {
      // 
      this.$refs.dataAddForm.validate((valid) => {


        if (valid) {
          // ajax
          // const param = this.$encrypTion(JSON.stringify(this.formData))
          this.$http.post('/api/types2/add', this.formData).then((res) => {
            // 
            this.dialogFormVisible = false
            if (res.data.flag) {
              // 
              this.findPage()
              // 
              this.$message({
                message: '',
                type: 'success'
              })
            } else { // 
              this.$message.error(res.data.code)
            }
          })
        } else { // 
          this.$message.error('')
          return false
        }
      })
    },


    //  switch 
     recommendStateChanged (row) {
      

      console.log('row',row)

       if(row.recommend==true) {

            // put
        this.$axios.put('/blog',row).then(res=>{ 

            if(res.data.flag){
              this.$message.success("")
            }

        })

       }

       else{ 

         // put
        this.$axios.put('/blog',row).then(res=>{ 

            if(res.data.flag){
              this.$message.error("")
            }

        })
       
      }
       
      
     },


    


    logout () {
      window.sessionStorage.clear()
      this.$router.push('/login')
      // vuex
      window.location.reload()
    },


    // 
    handleCurrentChange (currentPage) {
      // 
      this.pagination.currentPage = currentPage
      // findPage
      this.findPage()
    },



    handleSizeChange (newSize) {
      this.pagination.pageSize = newSize
      this.findPage()
    },



    // 
    resetForm () {
      this.formData = {}// 
      this.$refs.dataAddForm.resetFields()
    },


    // 
    handleCreate () {
      this.dialogFormVisible = true
      this.resetForm()
    },


    handleDelete (blogid) {
     


     this.$confirm("","",{type:"info"}).then(()=>{
           
                    this.$axios.delete('/blog/'+blogid).then((res)=>{


                            if(res.data.flag)
                            this.$message.info("");

                      this.findPage()
                      this.$router.push('/AdminHome/Blogs')   


                    })
                }).catch(()=>{
                    this.$message.info("");

                
     })
    },

  


  
    handleUpdate (uid) {
      
      sessionStorage.setItem('admin_blogid',uid)
      this.$router.push('/AdminHome/changeUser')
     
    }

  }



}
</script>

<style lang="less" scoped>
  @import "../../assets/css/style.css";
  @import "../../assets/css/me.css";
  .blogs {
    height: 100%;
    margin: 0;
    padding: 0;
  }
</style>
