<script>
export default {
  data () {
    return {
      imageUrl: '',
      verifyCode: '',
    
    


    //    
    formData: {
        username: '',
        password: '',
    
      
        avatar: 'https://unsplash.it/800/450?image= 1032  '
      },
      dialogFormVisible: false, // 
    
    
    
    
    
    //       
      loginForm: {
        username: '',
        password: '',
        verKey: '',
        code: ''
      },


      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: ' 6  20 ', trigger: 'blur' }
        ],
       
        nickname: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 15, message: ' 3  15 ', trigger: 'blur' }
        ]
      }
    }
  },



  created () {
    this.getVerifyCode()
  },


  methods: {
    masterFileMax (files, fileList) {
      console.log(files, fileList)
      this.$message.warning('')
    },
    async uploadPic (param) {
      var fileObj = param.file
      var form = new FormData()
    

    // 
      form.append('file', fileObj)
      const { data: res } = await this.$http.post('/serverApi/oss/userAvatar', form)
      if (res.flag) {
        // 
        this.$message.success('')
        this.formData.avatar = res.data.url
        console.log(res.data.url)
      } else { // 
        this.$message.error(res.message)
      }
    },



    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },


    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt5M = file.size / 1024 / 1024 < 5

      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt5M) {
        this.$message.error('5MB!')
      }
      return isJPG && isLt5M
    },






    async getVerifyCode () {
      this.$axios.get('/code/getCode').then(res=> {  
      
      this.verifyCode = res.data
      
      this.loginForm.verKey = res.code
      
      }

    
      )
      
      // this.$message({
      //   message: '',
      //   type: 'info'
      // })
    },



    // 
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    // 
    handleCreate () {
      this.dialogFormVisible = true
    },



    regist () {

      // 
      this.$refs.registForm.validate((valid) => {

        if (valid) {

        
         
          this.$axios.post('/user',this.formData).then((res) => {

        

          // 
            this.dialogFormVisible = false
            if (res.data.flag) {
              // 
              this.$message({
                message: '',
                type: 'success'
              })
            }



          })
        } 
        
        



      })
    },





        //     token

    login () {

      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
       
         
        this.$axios.get('/user/login'+'/'+this.loginForm.username).then(res=>{ 
          
         
          
         window.sessionStorage.setItem('uid',res.data.data.uid)
        window.sessionStorage.setItem('username',res.data.data.username)



          console.log('login_token',res.data)
            // 1.  token sessionStorage 
          //   1.1 API
          //   1.2 token  token  sessionStorage 
         
         window.sessionStorage.setItem('token', res.data.data.token)
        //  JSON.stringify  
        // sessionStorage
         window.sessionStorage.setItem('user', JSON.stringify(res.data.data))

            
            // 2.            
           if(this.loginForm.password==res.data.data.password)
            {   


                if(this.loginForm.username=='admin')
                  {
                    this.$message.success('')
                    this.$router.push('/adminHome')

                  }
                  
                  else{
                     this.$message.success('')
                this.$router.push('/home')
                  }
               


            }
            else{

               this.$message.error(",")
               this.resetLoginForm()
            }
           
        })
        
        
          
          
          
         
        


      })

      }



  }
}
</script>

