<template>
  
  
      <!-- 2. EChartsDom -->
     <div id="myChart" :style="{width: '800px', height: '600px',marginLeft:'350px'}"></div>
  

</template>





<script>
export default {

mounted (){
     
     
         
        this.$axios.get('/echart/barVOListView').then(res=>{ 

            console.log(res.data.data.values)
          //    
          const echarts = require('echarts')
          // domecharts
        let myChart = echarts.init(document.getElementById('myChart'))

          //    Vue this - - - - let myChart = this.$echarts.init(document.getElementById('myChart'))
        

      
        // 
        myChart.setOption({
          title: {
            //     
            text: '',
            left: 'center',
            top: 20,
            textStyle: {
              color: '#555555'
            }
          },

          tooltip: {},
          // x data 
          xAxis: {
            data: res.data.data.names,

            axisLabel: {
            interval: 0, //0
            rotate: 25, // x 25  
          }
          },
          yAxis: {},


            // series   
          series: [{
            name: '',
            //    type  piebar 
            type: 'bar',  
            //    data views
            data: res.data.data.values
          }]
        })
           
        })
        
       
    

    }
}
</script>


