<template>
  <div class="blogs">
    <div class="content-header">
      <h1><small></small></h1>
      <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
        
      </el-breadcrumb>
    </div>

    <!---->
    <div class="app-container">
      <div class="box">




        <el-form :model="formData" :rules="userRules" ref="userForm" label-width="100px">
         
           <!--   -->
          <el-row>
            <el-col :span="8">
              <el-form-item prop="username" label="" >
                <el-input v-model="formData.username" ></el-input>
              </el-form-item>
            </el-col>


            <el-col :span="8">
              <el-form-item prop="password" label="">
                <el-input v-model="formData.password"></el-input>
              </el-form-item>
            </el-col>
            
          </el-row>
         


                <!--   -->
            <el-row>
            <el-col :span="8">
              <el-form-item prop="username" label="" >
                <el-input :value="state" type="text"></el-input>
              </el-form-item>
            </el-col>


            <el-col :span="8">
              <el-form-item prop="password" label="" v-show="formData.isFlag==1">
               
                    <el-switch  v-model="value" @change="accept">     </el-switch>

              </el-form-item>
            </el-col>
            
          </el-row>

            

           


          <el-form-item>

            <el-button type="primary" @click="submitForm" ></el-button>
<!--            <el-button @click="resetForm('formData')"></el-button>-->
          </el-form-item>




        </el-form>

        
      </div>
    </div>
    <br>
    <br>
  </div>
</template>

<script>
export default {
  data () {
    return {
        value:false,
      imageUrl: '',

    // 
      formData: {},



      userRules: {
        // 
        
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: ' 6  20 ', trigger: 'blur' }
        ],
      
        avatar: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1, max: 150, message: ' 1  150 ', trigger: 'blur' }
        ]
      }
    }
  },


  created () {


    this.getUser()


  },



 computed:{
     state(){

                 let text='' 
            if(this.formData.isFlag==0)
            { 
                text = ''
                }

            else if(this.formData.isFlag==1)
            {
                text=''
                }

            else if(this.formData.isFlag==2)
            {
                text=''
            }
            else if(this.formData.isFlag==-1)
            {
                text=''
            }

          return text

     }

 },

  methods: {

      accept(){

                this.formData.isFlag = 2
                this.formData.blogger = 1

                this.$message.success('')
      
      },


    masterFileMax (files, fileList) {
      console.log(files, fileList)
      this.$message.warning('')
    },




     uploadPic (param) {
      var fileObj = param.file
      var form = new FormData()
      // 
      form.append('file', fileObj)
      const { data: res } =  this.$http.post('/serverApi/oss/userAvatar', form)
      if (res.flag) {
        // 
        this.$message.success('')
        this.formData.avatar = res.data.url
        console.log(res.data.url)
      } else { // 
        this.$message.error(res.message)
      }
    },



    handleAvatarSuccess (res,file) {
      this.imageUrl = URL.createObjectURL(file.raw)
    },



    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt5M = file.size / 1024 / 1024 < 5

      if (!isJPG) {
        this.$message.error(' JPG !')
      }
      if (!isLt5M) {
        this.$message.error('5MB!')
      }
      return isJPG && isLt5M
    },



        //  
    submitForm () {
      // 
      this.$refs.userForm.validate((valid) => {
        if (valid) 
        
        {
          // ajax
          this.formData.uid = sessionStorage.getItem('admin_blogid')
          
          console.log(this.formData.username)
          console.log(this.formData.uid)
          this.$axios.put('/user', this.formData).then((res) => {
            

           


            
              if(res.data.flag) {
              // 
              this.$message({
                message: '',
                type: 'success'
              })
            } 
            
            
            else { // 
              this.$message.error(res.data.message)
            }
                    this.getUser()
          })
        } 
        
        
        
        else 
        { // 
          this.$message.error('')
          return false
        }


      })
    },


    resetForm (formName) {
      this.$refs[formName].resetFields()
    },




      // 
    getUser () {


       this.$axios.get('/user/'+sessionStorage.getItem('admin_blogid')).then((res) => {
            // 

            
            this.formData = res.data.data
           
          })



    }
  }





}
</script>

